@use 'base' as *;

.container{
  display: grid;
  min-width: 100%;
  min-height: auto;
  grid-template-columns: repeat(auto-fit,minmax(30rem
  ,1fr));
  column-gap: 3rem;
  row-gap: 3rem;
  position: relative;
  padding: 2rem;
  height: 50rem;
  
}
.paintings{
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.paintings::after{
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg,rgba(255,255,255,.499872),rgba(255,255,255,.499872));
  opacity: 0;
  z-index: 0;
  transition: opacity 250ms;
}
.paintings::before{
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background-image: linear-gradient(177deg,rgba(0,0,0,.0001) 0,rgba(0,0,0,.841672) 100%);
}
.paintings:hover::after{
  opacity: 1;
}
.gallery-img{
  width: 100%;
  grid: 'content' 1fr/1fr;
  

}
.grid-container{
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
.heading-cointainer{
  position:absolute;
  display: flex;
  flex-direction: column;
  bottom: 15%;
  left: 2rem;
  row-gap: 1rem;
  padding: 0 2rem;
  color: #fff;
}
.heading{
  grid-area: content;
  font-size:2.5rem;
}
.artist{
  font-size: 1.2rem;
}
.navigation{
  padding:4rem 2rem;
  display: flex;
  align-items: center;
}
.slideshow-heading{
  margin-left: auto;
  color: $gray-500;
  letter-spacing: .2rem;
  word-spacing: .5rem;
  cursor: pointer;
  transition: all .2s ease-in-out;
}
.slideshow-heading:hover{
  transform: scaleX(.96);
}
.gallery-container{
  display: grid;
  grid-template-columns: 50rem 15rem 1fr;
  position: relative;
  padding: 4rem;
  column-gap: 3rem;
  justify-content: center;
  @media only screen and (max-width:$bp-pc) {
    grid-template-columns: 60rem 10rem ;
    justify-content: center;
    column-gap: 6rem;
  }
  @media only screen and (max-width:$bp-mobile) {
    grid-template-columns: 1fr;
    justify-content: center;
    row-gap: 2rem;
  }
}
.image-artist{
  width: 100%;
  @media only screen and (max-width:$bp-pc) {
    width: 10rem;
    
  }
}
.art{
  position: relative;
  align-self: flex-end;
  margin-bottom: -6rem;
  @media only screen and (max-width:$bp-pc) {
    height:15rem;
    
  }
}
.img-gallery{
  width: 100%;
}
.figure{
  position: relative;
}
.view{
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  padding:1rem 1.5rem ;
  background-color: rgba($color: #000000, $alpha: .7);
  display: flex;
  color: $gray-500;
  column-gap: 1rem;
  align-items: center;
  cursor: pointer;
  transition: all .3s ease;
  @media only screen and (max-width:$bp-mobile) {
    bottom: 95%;
    
  }
  
}
.view:hover{
  background-color: rgba($color: $gray-500, $alpha: .8);
  color: #fff;
}
.view img{
  width: 1.5rem;
}
.view p{
  font-size: 1.5rem;
}

.header-tag{
  position: absolute;
  left: 100%;
  top: 0;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  background-color: #fff;
  padding: 2rem 4rem;
  padding-bottom: 16rem;
  z-index: 5;
  @media only screen and (max-width:$bp-pc) {
    padding: 2rem;
    left: 100%;
    transform: translateX(-100%);
  }
  @media only screen and (max-width:$bp-mobile) {
    top: 100%;
    transform: translateY(-90%);
    left: 0;
    padding: 2rem;
    padding-bottom: 2rem;
  }
}
.text{
  padding: 2rem 2rem 2rem 16rem;
  line-height: 2;
  color: $gray-500;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  column-gap: 6rem;
  @media only screen and (max-width:$bp-pc) {
    padding-left: 2rem;
    text-align: center;
    grid-column: 1/-1;
    margin: 0 auto;
  }
}

.source{
  margin-top: 6rem;
  font-size: 1.5rem;
  color: $gray-500;
  transition: all .2s ease-in-out;
}
.source:hover{
  transform: scaleX(.97);
}
.text p{
  font-size: 1.5rem;
  @media only screen and (max-width:$bp-pc) {
    font-size: 1.5rem;
  }
  
}
.header-heading{
  font-size: 6rem;
  @media only screen and (max-width:$bp-pc) {
    font-size: 4rem;
    margin-top: 4rem;
  }
}
.footer{
  margin-top: 2rem;
  display: flex;
  padding:2rem 4rem ;
  justify-content: space-between;
}
.footer-heading{
  font-size: 3rem;
}
.artist-footer{
  font-size: 1.2rem;
}
.container-gallery{
  display: none;
}